import { alpha } from '@mui/material/styles';

// ----------------------------------------------------------------------

export default function Backdrop(theme) {
  const varLow = alpha(theme.palette.grey[900], 0.2);
  const varHigh = alpha(theme.palette.grey[900], 1);

  return {
    MuiBackdrop: {
      styleOverrides: {
        root: {
          background: [
            `rgb(13, 15, 18)`,
            `-moz-linear-gradient(120deg, ${varLow} 0%, ${varHigh} 100%)`,
            `-webkit-linear-gradient(120deg, ${varLow} 0%, ${varHigh} 100%)`,
            `linear-gradient(120deg, ${varLow} 0%, ${varHigh} 100%)`
          ],
          '&.MuiBackdrop-invisible': {
            background: 'transparent'
          }
        }
      }
    }
  };
}
